body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f6f6f6;
}

#root {
    display: flex;
    align-items: center;
    justify-content: center;
    /*background-color: white;*/
}

.stats {
    width: 1200px;
    height: 500px;
    background-color: #ffffff;
    border-radius: 0 0 20px 20px;
    display: flex;
    justify-content: center;
    align-items: center;
}

.list {
    width: 1200px;
    min-height: 500px;
    background-color: white;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: center;
}

.circle {
    display: flex;
    justify-content: center;
    align-items: center;
    width: 300px;
    height: 300px;
    border-radius: 50%;
    box-shadow: rgba(0, 0, 0, 0.5) 0 0 3px;
}

.details {
    flex-direction: column;
    margin-left: 100px;
    width: 600px;
    min-height: 400px;
    border-radius: 10px;
    box-shadow: rgba(0, 0, 0, 0.5) 0 0 3px;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
}

.details_text {

}

.item {
    margin-top: 15px;
    width: 1150px;
    min-height: 100px;
    display: flex;
    flex-direction: column;
    justify-content: center;
}

h1 {
    margin: 5px 0 0 15px;
    font-weight: normal;
}

h2 {
    margin: 0 0 5px 15px;
    font-weight: lighter;
}

.paths {
    font-size: 16px;
    margin-left: 50px;
}

.ant-collapse-header-text {
    /*display: ;*/
}

.count {
    display: flex;
    position: absolute;
    top: 9px;
    left: 1050px;
    font-size: 50px;
    color: red;
    align-items: center;
    justify-content: center;
    width: 50px;
}

.ant-progress-inner {
    width: 290px !important;
    height: 290px !important;
    font-size: 50px !important;
}

.text {
    margin: 13px 0 13px 30px;
    font-size: 25px;
}

.path-input {
    width: 590px;
    margin: 10px 5px 5px 5px;
    font-size: 25px;
}

.loading {
    display: flex;
    justify-content: center;
    align-items: center;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100vh;
    z-index: 1000;
    background-color: rgba(0, 0, 0, 0.52);
}

#error {
    margin-left: 10px;
    font-size: 20px;
    color: red;
    display: none;
}
